import ScrollReveal from "scrollreveal"

export function reveal01(name,index) {
    ScrollReveal().reveal(name, {
        // 
        duration: 2000,
        // 
        delay: 200*index,
        // 'bottom', 'left', 'top', 'right'
        origin: 'top',
        // 
        reset: false,
        // 
        mobile: false,
        // %rem
        distance: '200px',
        // 
        opacity: 0.001,
        easing: 'linear',
        scale: 0.9,
        beforeReveal: function (ele) {
        }
    })

}
